import { createMenu } from "./components/menu.js";
import { createTripInfo } from "./components/tripInfo.js";
import { createTripFilters } from "./components/tripFilters.js";
import { createSort } from "./components/sort.js";
import { createForm } from "./components/form.js";
import { createTripDay, createTripEventMarkup } from "./components/tripDay.js";
import {generateEvents} from './mock/events.js';

// Variables
const tripInfo = document.querySelector('.trip-main__trip-info');
const tripNav = document.querySelector('.trip-controls__trip-tabs');
const tripControls = document.querySelector('.trip-main__trip-controls');
const tripEvents = document.querySelector('.trip-events');
const tripDays = document.querySelector('.trip-days');

const COUNT_EVENTS = 10;
const events = generateEvents(COUNT_EVENTS);
// Functions
const render = (parent, element, position) => {
   parent.insertAdjacentHTML(position, element);
};

events.sort(function(a,b){return a.date_from.getTime() - b.date_from.getTime()}); // сортировка массива данных по возрастанию даты


const groupByDays = events.reduce((acc, elem)=> {
    const date = elem.date_from;
    if (acc[date]) {
        acc[date].push(elem)
      } else {
        acc[date] = [elem]
      }
      return acc
}, {});



const eventsMarkup = events.map((item) => createTripEventMarkup(item));
const dates = events.map((eventMock) => {
    return eventMock.date_from
});

const setDate = new Set(dates);
const COUNT_OF_DAYS = setDate.size;


render(tripInfo, createMenu(), 'afterbegin');
render(tripNav, createMenu(), 'afterbegin');
render(tripControls, createTripFilters(), 'afterbegin');
render(tripEvents, createForm(events[0]), 'afterbegin');
render(tripEvents, createSort(), 'afterbegin');
/* for(let i = 1; i <= COUNT_OF_DAYS ; i++) {
    render(tripDays, createTripDay(groupByDays[setDate[i]], i), 'beforeend');
}; */

let counter = 1
for(let date of setDate) {
    console.log(groupByDays[date])
    render(tripDays, createTripDay(groupByDays[date][0],groupByDays[date], counter), 'beforeend');
    counter++
}







